@import "helpers/all";
@import "generated/sprite-svg";
@import "common";
@import "layouts";
@import "main";


.container {
	max-width: 1060px;
	margin: 0 auto; }

.header {
	display: flex;
	justify-content: space-between;
	align-items: baseline;
	width: 90%;
	margin: 0 auto;
	flex-direction: row;
	max-width: 1200px;
	@include r(1000) {
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: 30px; }
	@include r(500) {
		flex-direction: column; } }

.logo {
	align-self: center;
	margin-right: 20px;
	@include r(1000) {
		margin: 0;
		order: 2; }
	@include r(650) {
		order: 1; }
	img {
		display: inline-block; } }

.navigation {
		@include r(1000) {
			// margin-right: 100px
			order: 1;
			min-width: 80vw;
			margin: 0 auto;
			margin-top: 10px;
			margin-left: 80px;
			display: flex;
			justify-content: center; }
		@include r(650) {
			order: 3;
			margin: 0; } }

.nav {
	position: relative;
	margin: 0 auto;
	margin-right: 0;
	margin-bottom: 20px;
	@include r(1000) {
		margin: 0;
 }		// margin-left: 30px

	@include r(650) {
		min-width: 50vw;
		left: 50%;
		transform: translate(-40%, 0); }
	@include r(500) {
		margin: 0;
		display: flex;
		flex-direction: column;
		align-items: center;
		transform: translate(10%, 0);
		left: 0;
		right: 0; }

	li {
		display: inline-block;
		@include r(500) {
			margin-bottom: 15px; } }

	a {
		color: #515151;
		display: inline-block;
		position: relative;
		margin-right: 30px;
		font-weight: bold;
		text-decoration: none;
		font-size: 18px;
		font-family: sans-serif; } }
.list-down {
	color: red;
	position: relative;
	margin-right: 40px;
	width: auto;
	@include r(500) {
		margin-right: 0;
		margin-bottom: 50px; }
	.icon {
		position: absolute;
		margin-left: 2px;
		right: 0;
		bottom: 50%;
		transform: translate(100%, 50%); } }
.list-down:hover {

	.nav-down {
		opacity: 1;
		visibility: visible; } }


.nav-down {
	opacity: 0;
	visibility: hidden;
	transition: all .63s ease-out .15s;
	// Добавляем анимацию 0.3 сек. и задержку в 0.15 сек.
	position: absolute;
	font-size: 1rem;
	background: rgba(0,0,0,.6);
	li {
		width: 100%;
		&:last-child {
			padding-bottom: 5%; }
		a {
			padding: 5% 5% 0;


			margin: 0;
			color: #FFF;
			text-align: center; } } }

