.header {
	button {
		font-size: 24px;
		color: #FFF;
		background: green;
		padding: 10px;
		line-height: 1.4;
		min-width: 165px;
		@include r(1000) {
			order: 3; }
		@include r(650) {
			order: 2; }
		@include r(500) {
			margin-bottom: 25px; } } }

.photo {
	.container {
		margin-bottom: 7%; }
	img {
		width: 100%; } }

.section {
	&__title {
		font-size: 2em;
		position: relative;
		font-weight: bold;
		margin-bottom: 7%;
		text-align: center;
		padding: 0 5% 5%;
		@include r(650) {
			font-size: 4em; }
		&:after {
			content: "";
			width: 35%;
			left: 50%;
			transform: translate(-50%,0);
			height: 3px;
			background: #000;
			position: absolute;
			bottom: -7%; } }
	&__text {
		font-size: 18px;
		padding: 7%;
		text-indent: 1.5em; } }






