html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arrow {
  width: 1em;
  height: 1em;
  fill: #515151;
}

body {
  background: #FFF;
  font-size: 50px;
  font-size: calc(16px + (50 - 16) * ((100vw - 600px) / (1000 - 600)));
  line-height: 1.4;
}

@media (max-width: 600px) {
  body {
    font-size: 16px;
  }
}

@media (min-width: 1000px) {
  body {
    font-size: 50px;
  }
}

.block {
  display: flex;
}

.container {
  max-width: 1060px;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 90%;
  margin: 0 auto;
  flex-direction: row;
  max-width: 1200px;
}

@media only screen and (max-width: 1000px) {
  .header {
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
}

@media only screen and (max-width: 500px) {
  .header {
    flex-direction: column;
  }
}

.logo {
  align-self: center;
  margin-right: 20px;
}

@media only screen and (max-width: 1000px) {
  .logo {
    margin: 0;
    order: 2;
  }
}

@media only screen and (max-width: 650px) {
  .logo {
    order: 1;
  }
}

.logo img {
  display: inline-block;
}

@media only screen and (max-width: 1000px) {
  .navigation {
    order: 1;
    min-width: 80vw;
    margin: 0 auto;
    margin-top: 10px;
    margin-left: 80px;
    display: flex;
    justify-content: center;
  }
}

@media only screen and (max-width: 650px) {
  .navigation {
    order: 3;
    margin: 0;
  }
}

.nav {
  position: relative;
  margin: 0 auto;
  margin-right: 0;
  margin-bottom: 20px;
}

@media only screen and (max-width: 1000px) {
  .nav {
    margin: 0;
  }
}

@media only screen and (max-width: 650px) {
  .nav {
    min-width: 50vw;
    left: 50%;
    transform: translate(-40%, 0);
  }
}

@media only screen and (max-width: 500px) {
  .nav {
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: translate(10%, 0);
    left: 0;
    right: 0;
  }
}

.nav li {
  display: inline-block;
}

@media only screen and (max-width: 500px) {
  .nav li {
    margin-bottom: 15px;
  }
}

.nav a {
  color: #515151;
  display: inline-block;
  position: relative;
  margin-right: 30px;
  font-weight: bold;
  text-decoration: none;
  font-size: 18px;
  font-family: sans-serif;
}

.list-down {
  color: red;
  position: relative;
  margin-right: 40px;
  width: auto;
}

@media only screen and (max-width: 500px) {
  .list-down {
    margin-right: 0;
    margin-bottom: 50px;
  }
}

.list-down .icon {
  position: absolute;
  margin-left: 2px;
  right: 0;
  bottom: 50%;
  transform: translate(100%, 50%);
}

.list-down:hover .nav-down {
  opacity: 1;
  visibility: visible;
}

.nav-down {
  opacity: 0;
  visibility: hidden;
  transition: all .63s ease-out .15s;
  position: absolute;
  font-size: 1rem;
  background: rgba(0, 0, 0, 0.6);
}

.nav-down li {
  width: 100%;
}

.nav-down li:last-child {
  padding-bottom: 5%;
}

.nav-down li a {
  padding: 5% 5% 0;
  margin: 0;
  color: #FFF;
  text-align: center;
}

.header button {
  font-size: 24px;
  color: #FFF;
  background: green;
  padding: 10px;
  line-height: 1.4;
  min-width: 165px;
}

@media only screen and (max-width: 1000px) {
  .header button {
    order: 3;
  }
}

@media only screen and (max-width: 650px) {
  .header button {
    order: 2;
  }
}

@media only screen and (max-width: 500px) {
  .header button {
    margin-bottom: 25px;
  }
}

.photo .container {
  margin-bottom: 7%;
}

.photo img {
  width: 100%;
}

.section__title {
  font-size: 2em;
  position: relative;
  font-weight: bold;
  margin-bottom: 7%;
  text-align: center;
  padding: 0 5% 5%;
}

@media only screen and (max-width: 650px) {
  .section__title {
    font-size: 4em;
  }
}

.section__title:after {
  content: "";
  width: 35%;
  left: 50%;
  transform: translate(-50%, 0);
  height: 3px;
  background: #000;
  position: absolute;
  bottom: -7%;
}

.section__text {
  font-size: 18px;
  padding: 7%;
  text-indent: 1.5em;
}
